body {
  min-height: 100%;
  width: 100%;
  margin: 0 auto; }

.side-bar[data-position="right"] {
  position: absolute;
  top: 50%;
  right: 0;
  -webkit-transform: translateY(-50%);
  transform: translateY(-50%);
  min-height: 100%;
  width: 64px;
  background-color: #43a;
  border-top-left-radius: 6px;
  border-bottom-left-radius: 6px; }

.side-bar[data-position="left"] {
  position: absolute;
  left: 275px;
  top: 50%;
  -webkit-transform: translateY(-50%);
  transform: translateY(-50%);
  min-height: 100%;
  width: 64px;
  background-color: #333;
  z-index: 500; }

div.item {
  color: white;
  height: 64px;
  width: 64px;
  background-color: #444;
  line-height: 64px;
  text-align: center;
  font-size: 20px; }

div.item.bottom {
  position: absolute;
  bottom: 75px; }

.items {
  margin-top: 56px; }

div.heading {
  font-size: 16px;
  font-family: 'Lato', sans-serif;
  color: white;
  height: 65px;
  line-height: 44px;
  font-weight: 700;
  padding: 10px;
  background-color: #333; }

div.item:hover {
  background-color: #ff0138b3;
  cursor: pointer; }

div.side-panel {
  z-index: 999;
  left: -275px;
  position: absolute;
  min-width: 280px;
  min-height: 94%;
  background-color: #222;
  display: inline-block; }

div.side-panel.open {
  -webkit-animation-name: sidebar-open;
          animation-name: sidebar-open;
  -webkit-animation-duration: .25s;
          animation-duration: .25s;
  left: 0; }

div.side-panel.close {
  -webkit-animation-name: sidebar-close;
          animation-name: sidebar-close;
  -webkit-animation-duration: .25s;
          animation-duration: .25s;
  left: -275px; }

/* Keyframes */
@-webkit-keyframes sidebar-open {
  0% {
    left: -275px; }
  100% {
    left: 0; } }
@keyframes sidebar-open {
  0% {
    left: -275px; }
  100% {
    left: 0; } }

@-webkit-keyframes sidebar-close {
  0% {
    left: 0; }
  100% {
    left: -275px; } }

@keyframes sidebar-close {
  0% {
    left: 0; }
  100% {
    left: -275px; } }

